import noData from '../components/noData'
import loading from '../components/loading'
export default {
  name: 'news',
  components: { noData, loading },
  data () {
    return {
      loading: false,
      newsList: []
    }
  },
  computed: {
    noNews () {
      return this.newsList.length === 0
    }
  },
  mounted () {
    this.getList()
  },
  methods: {
    getList () {
      this.loading = true
      const a = {
        id: '1',
        title: '“大众点评”即将消失：被改名或被直接放弃',
        author: '春雨',
        img: 'https://ss2.baidu.com/6ONYsjip0QIZ8tyhnq/it/u=1734052457,4245591854&fm=173&app=49&f=JPEG?w=218&h=146&s=61E22FE7427515960720A12903006053',
        time: '2019-01-01'
      }
      const b = {
        id: '2',
        title: '日本提出向中国人收“二维码使用费”？内容不实日本提出向中国人收“二维码使用费”？内容不实',
        author: '是大润发',
        img: '',
        time: '2019-01-02'
      }
      this.newsList = [a, a, b, a, a, b, a, a]
      this.loading = false
    },
    jumpToDetail (id) {
      this.$router.push({ path: '/news/' + id })
    }
  }
}



